// Imports
import React from 'react';
import Popup from "reactjs-popup";

// Imports App
import '../index.css';

export default class Victory extends React.Component {
   
  createPopUpVictory(mess){
      return(
      <Popup modal trigger={<button>Restart</button>}>{mess}</Popup>
      )
  }

  render() {
    const victory = [];
    if (this.props.victory.site !== -1){
        victory.push(this.createPopUpVictory(this.props.victory.mess))
    }
    return (
      <div>
        {victory}
      </div>
    );
  }
}
